.expense-item {
    border-style: inset;
    border-color: white;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 12px;
}

.expense-item__description {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1 1;
}

.expense-item h2 {
    font-size: medium;
    flex: 1 1;
    margin: 0 1rem;
    color: #519872;
}

.expense-item__price {
    font-size: small;
    font-weight: bold;
    color: crimson;


    border: 1px solid black;
    padding: 0.5rem;
    border-radius: 12px;
}

@media (min-width: 500px) {
    .expense-item__description {
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex: 1 1;
    }

    .expense-item__description h2 {
        font-size: 1.25rem;
    }

    .expense-item__price {
        font-size: 1.25rem;
        padding: 0.5rem 1.5rem;
    }
}
